.sidebar-header .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    background: #fff;
    padding: 1rem;
    width: 18rem;
    border-right: 1px solid #e5e5e5;
    z-index: 11;
    transition: 0.3s ease;
}
@media (max-width: 991px) {
    .sidebar-header .sidebar {
        width: 0;
        opacity: 0;
        pointer-events: none;
   }
    .sidebar-header .sidebar.open {
        width: 18rem;
        opacity: 1;
        pointer-events: auto;
   }
}
.sidebar-header .sidebar .logo {
    padding: 0 0.5rem;
    margin-bottom: 3rem;
}
.sidebar-header .sidebar .logo img {
    max-height: 7rem;
}
.sidebar-header .sidebar .menus ul li {
    margin: 0.25rem 0;
}
.sidebar-header .sidebar .menus ul li a {
    padding: 0.75rem 1rem;
    border-radius: 12px;
    transition: 0.3s ease-in;
}
.sidebar-header .sidebar .menus ul li a:hover, .sidebar-header .sidebar .menus ul li a.active {
    color: #000;
    background: #c5bebe;
}
.sidebar-header .sidebar .menus ul li a .icon {
    margin-right: 0.5rem;
}
.sidebar-header .sidebar .menus ul li a .icon img {
    width: 1.5rem;
}
.sidebar-header .sidebar .menus ul li a .icon.down {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-50%, -50%);
}
.sidebar-header .sidebar .menus ul li ul {
    overflow: hidden;
    transition: height 0.3s ease-in-out;
}
.sidebar-header .sidebar .menus ul li ul li a {
    padding-left: 2rem;
}
.sidebar-header .header {
    position: fixed;
    top: 0;
    right: 0;
    width: calc(100% - 18rem);
    background: #fff;
    padding: 0.5rem 1rem 0.5rem 1.5rem;
    border-bottom: 1px solid #e5e5e5;
    z-index: 9;
    transition: 0.1s ease;
    display: none;
}
@media (max-width: 991px) {
    .sidebar-header .header {
        width: 100%;
        display: flex;
   }
}
.sidebar-header .header .header-left .icon-par, .sidebar-header .header .header-left .res-logo {
    margin-right: 0.5rem;
}
.sidebar-header .header .header-left .icon-par img, .sidebar-header .header .header-left .res-logo img, .sidebar-header .header img {
    max-height: 35px;
}
@media (min-width: 990px) {
    .sidebar-header .header .header-left .icon-par, .sidebar-header .header .header-left .res-logo {
        display: none;
   }
}
.profile {
    width: 2.5rem;
    height: 2.5rem;
    background: #00ACDC;
    color: #fff;
    border-radius: 50%;
    margin-right: 1rem;
}
.profile-dropdown {
    position: fixed;
    background: #fff;
    box-shadow: 0px 3px 10px 0 rgba(0, 0, 0, 0.07), 0 2px 9px 0 rgba(0, 0, 0, 0.06);
    border: none;
    padding: 8px 28px 24px;
    min-width: 352px;
    max-width: 100%;
    top: 70px;
    right: 24px;
    border-radius: 12px;
    transition: 0.3s ease;
    margin-top: 30px;
    opacity: 0;
    pointer-events: none;
    z-index: 9;
}
.profile-dropdown.showProfile {
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
}
.profile-dropdown .overlay {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    background: transparent;
}
.profile-dropdown .profile-dropdown-inner {
    position: relative;
    z-index: 2;
}
.profile-dropdown .profile-dropdown-inner .profile-text {
    font-size: 1rem;
    padding: 16px 0;
}
.profile-dropdown .profile-dropdown-inner .profile-main {
    padding: 16px 0 24px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px dashed #CCCCCC;
    margin-bottom: 12px;
}
.profile-dropdown .profile-dropdown-inner .profile-main .profile-detail {
    margin-left: 1rem;
}
.profile-dropdown .profile-dropdown-inner .profile-main .profile-detail h6 {
    margin-bottom: 0.25rem !important;
    font-size: 0.875rem;
    font-weight: 600;
    max-width: 100%;
}
.profile-dropdown .profile-dropdown-inner .profile-main .profile-detail span {
    font-size: 14px;
}
.profile-dropdown .profile-dropdown-inner .user-links {
    border-bottom: 1px dashed #CCCCCC;
    padding-bottom: 12px;
    margin-bottom: 24px;
}
.profile-dropdown .profile-dropdown-inner .user-links .user-links-inner a {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #6d727e;
    font-size: 14px;
    padding: 10px 15px;
    border-radius: 8px;
    transition: 0.3s ease;
}
.profile-dropdown .profile-dropdown-inner .user-links .user-links-inner a:hover {
    background: #e5e5e5;
}
.profile-dropdown .profile-dropdown-inner .user-links .user-links-inner a .button-icon {
    margin-right: 0.5rem;
}
.profile-dropdown .profile-dropdown-inner .user-dropdown {
    border-bottom: 1px dashed #CCCCCC;
    padding-bottom: 24px;
    margin-bottom: 24px;
}
.overlay.sidebar-overlay {
    position: fixed;
    z-index: 10;
}
.sb-footer {
    position: absolute;
    bottom: 2rem;
    left: 1rem;
    right: 1rem;
}
.sb-footer:before {
    content: "";
    position: absolute;
    top: 0;
    left: -1rem;
    right: -1rem;
    height: 1px;
    background: #e5e5e5;
    transform: translateY(-2rem);
}
.sb-footer h4 {
    margin-bottom: 1rem;
}
