.home-page {
    height: 100vh;
    background: #d3d2d7;
}
.home-page .home-left {
    width: 50%;
    padding: 5rem;
}
.home-page h1 {
    font-size: 7rem;
    line-height: 1;
    margin-bottom: 1rem;
}
.home-page p {
    max-width: 25rem;
    font-size: 0.825rem;
}

.float-right{
    float: right;
    position: absolute !important;
    right: 5px;
    top: 15px;
}

.home-page .home-right {
    width: 50%;
}
.home-page .home-right img {
    width: 100%;
    height: 100vh;
    object-fit: cover;
}
.home-page .home-left .button {
    width: auto;
    margin-top: 1.5rem;
}
.home-page .home-left .logo {
    width: 5rem;
    position: fixed;
    top: 1rem;
    left: 5rem;
}



@media (max-width: 991px) {
    .home-page .home-left {
        position: relative;
        z-index: 2;
        width: 100%;
        padding: 2rem
    }
    .home-page .home-left .logo {
        left: 2rem;
    }
    .home-page .home-right {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 1;
    }
    .home-page h1 {
        font-size: 5rem;
    }
}