.login {
    min-height: 100vh;
}
.login .login-left {
    width: 40%;
    background: #e1c8a9;
}
@media (max-width: 1280px) {
    .login .login-left {
        width: 50%;
   }
}
.login .login-left .login-in {
    padding: 5rem;
}
.login .login-right {
    background: #f5f5f5;
    width: 60%;
    overflow: hidden;
}
@media (max-width: 1280px) {
    .login .login-right {
        width: 50%;
   }
}
.login .login-right img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.login-par .logo {
    max-width: 150px;
    margin-bottom: 1rem;
}
.login-par h2 {
    margin-bottom: 0.875rem;
}
.login-par .cont::before {
    content: "";
    width: 100%;
    height: 1px;
    background: #6d727e;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}
.login-par .cont span {
    padding: 0.25rem 0.5rem;
    background: #e1c8a9;
    position: relative;
    z-index: 2;
}
.login-par .form-control {
    margin-top: 1.5rem;
}
.forgot-pass {
    text-align: right;
    margin: 1.5rem 0 0.5rem;
}
.btm-text {
    margin-top: 1rem;
}
.google {
    margin: 1rem 0;
}
.submit-btn {
    margin-top: 2.5rem;
}
button.button.login-btn {
    margin-top: 1.5rem;
}

@media (max-width: 991px) {
    .login .login-right {
        display: none;
    }
    .login .login-left {
        width: 100%;
    }
    .login .login-left .login-in {
        padding: 2rem;
    }
    .login-par .logo {
        max-width: 80px;
    }
}