.customize-page {
    height: 100vh;
    padding: 0 2rem;
}
.full-height {
    height: 100%;
}
.customize-right ul li {
    text-align: center;
    margin-bottom: 0.5rem;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0px 3px 10px 0 rgba(0, 0, 0, 0.07), 0 2px 9px 0 rgba(0, 0, 0, 0.06);
    overflow: hidden;
}
.customize-right ul li .customize-position {
    padding: 0.5rem;
}
 .customize-right ul li:last-child {
     margin-bottom: 0;
  }
.customize-right ul li img.img {
    margin: 0 auto;
    display: block;
    width: 4.5rem;
}
.customize-right ul li {
    width: 6rem;
    border: 2px solid transparent;
}
.customize-right ul li:hover, .customize-right ul li.active {
    border: 2px solid #000000;
    border-radius: 12px;
}

.low-popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.low-popup h4 {
    margin-bottom: 0.5rem;
    font-weight: 700;
    text-align: center;
}
.low-popup .low-popup-inner {
    position: relative;
    z-index: 5;
    background: #fff;
    padding: 1rem 2rem;
    border-radius: 12px;
    box-shadow: 0px 3px 10px 0 rgba(0, 0, 0, 0.07), 0 2px 9px 0 rgba(0, 0, 0, 0.06);
    max-width: 30rem;
}
.customize-left .low-popup .low-popup-inner .lists .list {
    width: calc(33.33% - 1rem);
    margin: 0.5rem;
    border: 2px solid #ededed;
    border-radius: 12px;
}
.customize-center .low-popup .low-popup-inner .lists .list:hover {
    border: 2px solid #000;
}
.customize-center .low-popup .low-popup-inner .lists .list img.product-img {
    width: 7rem;
    height: 7rem;
    display: flex;
    object-fit: contain;
}
.customize-center .low-popup .low-popup-inner .lists .list span.product-text {
    display: block;
    text-align: center;
    padding: 0.5rem;
}
.customize-page-logo {
    width: 5rem;
    position: fixed;
    top: 1rem;
    left: 2rem;
}
.customize-center {
    margin: 0 2rem;
    border: 2px solid #dfdfdf;
    border-radius: 12px;
    padding: 1rem;
    width: 23rem;
}
.customize-left ul {
    max-width: 21.875rem;
    max-height: 20rem;
    overflow: auto;
}
.customize-left ul li.list {
    width: 4.375rem;
    height: 4.375rem;
    margin: 0.25rem;
    border: 2px solid #dfdfdf;
    padding: 0.25rem;
    border-radius: 12px;
}
.customize-left ul li.list:hover, .customize-left ul li.list.active {
    border: 2px solid #000;
}
.customize-left ul li.list img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12px;
}
.customize-center .custom-button {
    margin-top: 1rem;
}
@media (max-width: 991px) {
    .customize-left ul {
        max-width: 16rem;
    }
}
@media (max-width: 768px) {
    .customize-page .customize-right ul li {
        margin: 0.5rem;
    }
    .customize-right ul li img.img {
        width: 3rem;
    }
    .customize-left {
        width: 100%;
        margin-top: 1rem;
    }
    .customize-left ul {
        max-width: 100%;
    }
    .customize-center {
        margin: 0 0 0 2rem;
        width: 20rem;
    }
}
@media (max-width: 580px) {
    .customize-page {
        height: inherit;
        margin-top: 7rem;
        justify-content: center;
    }
    .customize-left ul li {
        margin: 0.5rem;
    }
}
@media (max-width: 480px) {
    .customize-right ul li {
        width: 4rem;
    }
    .customize-center {
        width: 17rem;
    }
}
@media (max-width: 380px) {
    .customize-page .customize-center {
        width: 14rem;
        margin: 0 0 0 1rem;
    }
    .customize-center .custom-button {
        font-size: 0.875rem !important;
    }
}