@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');
html {
    font-size: 18px;
}
@media all and (max-width: 1920px) {
    html {
        font-size: 16px;
   }
}
@media all and (max-width: 1680px) {
    html {
        font-size: 15px;
   }
}
@media all and (max-width: 1440px) {
    html {
        font-size: 14px;
   }
}
@media all and (max-width: 580px) {
    html {
        font-size: 13px;
   }
}
body {
    font-family: "Poppins", sans-serif !important;
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 400;
    min-height: 100%;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    background-color: #f5f5f5;
    color: #000;
}
.bold {
    font-weight: 700;
}
.semi-bold {
    font-weight: 600;
}
.medium {
    font-weight: 500;
}
.regular {
    font-weight: 400;
}
.light {
    font-weight: 300;
}
h1 {
    font-size: 1.75rem;
    line-height: 1.286;
    font-weight: 700;
}
h1.lg {
    font-size: 2.5rem;
    line-height: 1.2;
}
h1.sm {
    font-size: 2rem;
    line-height: 1.125;
}
h2 {
    font-size: 1.5rem;
    line-height: 1.33;
    font-weight: 700;
}
h3 {
    font-size: 1.25rem;
    line-height: 1.33;
    font-weight: 600;
}
h4 {
    font-size: 1.125rem;
    line-height: 1.45;
    font-weight: 500;
}
h5 {
    font-size: 0.875rem;
    line-height: 1.57;
    font-weight: 500;
}
p {
    font-size: 1rem;
    line-height: 1.5;
}
p.mini-lh {
    line-height: 1.3;
}
p.smini-lh {
    line-height: 1.25;
}
p.vsmini-lh {
    line-height: 1.125;
}
span.normal {
    font-size: 0.875rem;
    line-height: 1.5;
}
span.small {
    font-size: 0.75rem;
    line-height: 1.5;
}
span.mini-lh {
    line-height: 1.3;
}
span.smini-lh {
    line-height: 1.25;
}
span.vsmini-lh {
    line-height: 1.125;
}

*, ::after, ::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
*:focus, ::after:focus, ::before:focus {
    outline: none;
}
ol, ul {
    list-style: none;
    padding: 0;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
a {
    text-decoration: none;
}
.link {
    color: #4681ff;
}
.link:hover {
    text-decoration: underline;
}
img {
    max-width: 100%;
}
.text-center {
    text-align: center;
}
.full-width {
    width: 100%;
}
.inherit-height {
    height: inherit;
}
.block {
    display: block !important;
}
.none {
    display: none !important;
}
.inline-block {
    display: inline-block;
}
.relative {
    position: relative;
}
.flex {
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
}
.flex-column {
    -ms-flex-direction: column;
    flex-direction: column;
}
.align-items-center {
    -ms-flex-align: center;
    align-items: center;
}
.justify-content-start {
    -ms-flex-pack: start;
    justify-content: flex-start;
}
.justify-content-center {
    -ms-flex-pack: center;
    justify-content: center;
}
.justify-content-end {
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
}
.justify-content-between {
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.justify-content-around {
    -ms-flex-pack: distribute;
    justify-content: space-around;
}
.cursor {
    cursor: pointer;
}
.cursor-not-allow {
    cursor: not-allowed;
}
.rotate-45 {
    transform: rotate(45deg);
}
.rotate-90 {
    transform: rotate(90deg);
}
.rotate-180 {
    transform: rotate(180deg);
}
.filter-invert {
    filter: invert(1);
}
.text-control {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.text-control-two, .text-control-three {
    white-space: break-spaces;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}
.text-control-two {
    -webkit-line-clamp: 2;
}
.gray-one {
    color: #6d727e;
}
.gray-two {
    color: #595959;
}
.red-one {
    color: #FF0935;
}
.black_two {
    color: #171717;
}
.label_text {
    color: #7E8299;
}
.light-fill {
    fill: rgba(126, 130, 153, 0.7);
}
.label-fill {
    fill: #7E8299;
}
.red-fill {
    fill: #FF0935;
}
.pointer-none {
    pointer-events: none;
}
.disabled {
    opacity: 0.5;
    pointer-events: none;
    background: #F7F8FB !important;
}
.input-disabled {
    opacity: 0.5;
    background: #F7F8FB !important;
}

/* forms start */
.custom-field .MuiInputBase-root {
    width: 100%;  
}
.custom-field .MuiInputBase-root input, .custom-field .MuiSelect-select {
    padding: 0.5rem 1rem !important;
    
}
.custom-field.search .MuiInputBase-root {
    border-radius: 50px; 
}

/* forms end */

/* button start */
.button.custom-button {
    line-height: normal;
    font-weight: 500;
    border-radius: 4px;
    transition: 0.3s ease-in;
    font-family: "Poppins", sans-serif;
    border-radius: 8px;
    outline: none;
}
.button.custom-button.normal {
    padding: 0.33rem 1.25rem;
    font-size: 1rem;
}
.button.custom-button.small {
    padding: 0.25rem 0.5rem;
    font-size: 0.75rem;
}
.button.custom-button.black {
    background: #000;
    color: #fff;
    border: 1px solid #000;
}
.button.custom-button.black:hover, .button.custom-button.black:focus {
    background: #363434;
    border-color: #363434;
}
.button.custom-button.blue {
    background: #00ACDC;
    color: #fff;
    border: 1px solid #00ACDC;
}
.button.custom-button.blue:hover, .button.custom-button.blue:focus {
    background: #0585a9;
    border-color: #0585a9;
}
.button.custom-button.red {
    background: #FF0935;
    color: #fff;
    border: 1px solid #FF0935;
}
.button.custom-button.red:hover, .button.custom-button.red:focus {
    background: #d70505;
    border-color: #d70505;
}
.button.custom-button.light-red {
    background: #ffe7e4;
    color: #FF0935;
    border: 1px solid #ffe7e4;
}
.button.custom-button.light-red:hover, .button.custom-button.light-red:focus {
    background: #ffd8d3;
    border-color: #ffd8d3;
}
.button.custom-button.cancel {
    background: transparent;
    color: #000;
    border: 1px solid #CCCCCC;
}
.button.custom-button.cancel:hover, .button.custom-button.cancel:focus {
    border-color: #000;
}
.button.custom-button.icon-button.normal {
    padding-left: 1rem;
}
.button.custom-button.icon-button.normal .icon {
    width: 1.25rem;
    height: 1.25rem;
    font-size: 1.25rem;
    margin-right: 0.5rem;
    line-height: 0.7;
}
.button.custom-button.icon-button.small {
    padding-left: 0.35rem;
}
.button.custom-button.icon-button.small .icon {
    width: 1rem;
    height: 1rem;
    font-size: 1rem;
    margin-right: 0.25rem;
}
.button.custom-button.disabled-btn {
    background: #e7e3e3;
    border-color: #e7e3e3;
    color: #b9b6b6;
    pointer-events: none;
}
.button.custom-button.left-space {
    margin-left: 0.5rem;
}
.button.custom-button.radius {
    border-radius: 50px;
}

/* button end */

/* page layout start */
.page-container {
    padding: 1.5rem;
}
.page-container .page-title {
    margin-bottom: 1.5rem;
}
.page-container .body-lists {
    padding: 1rem;
    background: #fff;
    box-shadow: 0px 3px 10px 0 rgba(0, 0, 0, 0.07), 0 2px 9px 0 rgba(0, 0, 0, 0.06);
    border-radius: 10px;
    margin-bottom: 1.5rem;
    border: 1px solid #e5e5e5;
}
.page-container .body-lists.ph {
    padding: 3rem 2.5rem;
    background: #05eb7624;
}
.page-container .body-title {
    margin-bottom: 1rem;
}
.page-container .page-footer {
    position: sticky;
    bottom: 0;
    background: #fff;
    padding: 1rem;
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    margin-bottom: -1.5rem;
    border-left: 1px solid #e5e5e5;
    border-top: 1px solid #e5e5e5;
    z-index: 7;
}
.page-container .body-footer {
    text-align: right;
}
.base-body {
    margin: 0 0 0 18rem;
}
@media (max-width: 991px) {
    .base-body {
        margin: 3.5rem 0 0 0;
   }
}

/* pgee layout end */

/* popup start */
.center-popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
}
.stop-scroll {
    overflow-y: hidden;
}
.confirm-popup {
    background: #f5f5f5;
    border-radius: 16px;
    padding: 2rem;
    max-width: 520px;
}
.confirm-popup .icon-custom {
    width: 40px;
    height: 40px;
    margin-right: 8px;
    background-color: #FF0935;
}
.confirm-popup .icon-custom svg {
    fill: #FF0935;
}
.confirm-popup p {
    padding: 1.25rem 0 2rem;
}
.mat-mdc-dialog-surface {
    border-radius: 16px !important;
}
.upload-inner {
    position: relative;
    z-index: 2;
    background: #fff;
    max-width: 35rem;
    width: 100%;
    border-radius: 12px;
    overflow: hidden;
}
.upload-inner.right {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    border-radius: 12px 0 0 12px;
}
.upload-inner .upload-head {
    padding: 1.25rem 2rem;
    border: 1px solid #CCCCCC;
}
.upload-inner .upload-head .icon-custom {
    margin-right: 0.5rem;
}
.upload-inner .upload-body {
    padding: 1.5rem 1.75rem 2rem;
    max-height: calc(100vh - 9.25rem);
    overflow-y: auto;
    overflow-x: hidden;
}
.upload-inner .upload-footer {
    text-align: right;
    box-shadow: 0 0 9px #dddbdb;
    padding: 1rem 1.75rem;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
}
.upload-inner .form-control {
    margin-bottom: 1.5rem;
}
/* popup end */

/* icon start */
.icon-par {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50px;
    transition: 0.1s ease-in;
}
.icon-par:hover {
    background: #e5e5e5;
}
.icon-par .icon {
    font-size: 1.5rem;
}
.icon-par.small {
    width: 2rem;
    height: 2rem;
}
.icon-par.small .icon {
    font-size: 1.25rem;
    height: inherit;
    width: inherit;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

/* icon end */

/* table start */
.action-column {
    height: 100%;
}
.action-column .icon {
    display: flex;
    flex-wrap: wrap;
}
.img-text {
    p {
        width: 11.25rem;
    }
    img {
        width: 6.25rem;
        height: 3.75rem;
        object-fit: cover;
        border-radius: 0.75rem;
        margin-right: 0.75rem;
    }
    img.contain {
        object-fit: contain;
    }
}
/* table end */

/* upload file start */
.upload-file {
    background: #f5f5f5;
    border: 2px dashed #e5e5e5;
    border-radius: 12px;
}
.upload-file .file-type {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9;
    opacity: 0;
}
.upload-file img {
    padding: 3rem 7rem 5rem;
}
.upload-file .preview-img{
    padding: 0px !important;
}
.upload-file .drop-cont {
    position: absolute;
    bottom: 1rem;
    left: 0;
    right: 0;
    text-align: center;
}
.file-details {
    padding: 0.75rem;
    background: #f5f5f5;
    margin-top: 1rem;
    border-radius: 12px;
}
.file-details .file-details-left .icon {
    font-size: 3rem;
    width: 3rem;
    height: 3rem;
    margin-right: 1rem;
}
.file-details .file-details-left p {
    max-width: 20rem;
}

/* upload file end */

.model-page .form-control {
    margin-bottom: 1.5rem;
}
.model-page .form-control.left, .model-page .form-control.right {
    width: 50%;
}
.model-page .form-control.left {
    padding-right: 0.75rem;
}
.model-page .form-control.right {
    padding-left: 0.75rem;
}
.model-page .upload-file {
    width: 50%;
    margin-bottom: 1.5rem;
}
.model-page .upload-preview {
    width: calc(50% - 1.5rem);
    padding: 1rem;
    border: 2px solid #ccc;
    border-radius: 12px;
    margin: 0 0 1.5rem 1.5rem;
}
.model-page .upload-preview .upload-preview-text {
    height: 100%;
}
.model-page .upload-preview .upload-preview-text h4 {
    font-size: 2rem;
    font-weight: 700;
}
.model-page .upload-preview img {
    border-radius: 12px;
    height: 16rem;
    width: 100%;
    object-fit: contain;
}
.model-page .upload-preview .button {
    margin-top: 1.5rem;
}
.model-page .upload-file img {
    height: 22rem;
    margin: 0 auto;
}
.img-list-inner {
    width: calc(16.667% - 2rem);
    background: #e5e5e5;
    margin: 1rem;
    padding: 0.4rem;
    border-radius: 1rem;
}
.img-list-inner img {
    width: 200px;
    height: 180px;
    object-fit: contain;
    border-radius: 1rem;
}
.img-list-inner .icon-par {
    position: absolute;
    top: -1rem;
    right: -1rem;
    background: #f5f5f5;
}
.custom-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #00000052;
    z-index: 999;
}
.custom-loader span {
    width: 7rem !important;
    height: 7rem !important;
    color: #000;
}
@media (max-width: 1280px) {
    .img-list-inner {
        width: calc(25% - 2rem);
    }
    .model-page .upload-file img {
        height: 20rem;
        padding: 3rem 4rem 5rem;
    }
    .model-page .upload-preview img {
        height: 14rem;
    }
}
@media (max-width: 768px) {
    .model-page .form-control.left, .model-page .form-control.right {
        width: 100%;
    }
    .model-page .form-control.left {
        padding-right: 0;
    }
    .model-page .form-control.right {
        padding-left: 0;
    }
    .img-list-inner {
        width: calc(25% - 1rem);
        margin: 0.5rem;
        padding: 0.5rem;
    }
    .model-page .form-control.upload.full-upload img {
        height: 15rem;
    }
    .model-page .upload-file img {
        height: 17rem;
        padding: 2rem 2rem 5rem;
    }
    .model-page .upload-preview .upload-preview-text h4 {
        font-size: 1.5rem;
    }
    .model-page .upload-preview .upload-preview-text p {
        font-size: 0.875rem;
        margin-top: 0.5rem;
    }
    .model-page .upload-preview img {
        height: 11rem;
    }
}
@media (max-width: 580px) {
    .model-page .upload-file {
        width: 100%;
    }
    .model-page .upload-preview {
        width: 100%;
        margin: 0 0 1.5rem;
        min-height: 15rem;
    }
}
@media (max-width: 380px) {
    .img-list-inner {
        width: calc(33.33% - 1rem);
    }
    .model-page .form-control.upload.full-upload img {
        height: 12rem;
    }
}